import React, { useState, useRef, useEffect, useMemo } from 'react';
import AgoraRTC, {IAgoraRTCRemoteUser} from 'agora-rtc-sdk-ng';
import useAgora from './hooks/useAgora';
import useAgoraVolumes from './hooks/useAgoraVolumes';
import MediaPlayer from './components/MediaPlayer';
import './Call.css';

declare let ImageCapture: any;
declare let MediaRecorder: any;
declare let Blob: any;
declare let nw: any;

const fs = window.require ? window.require("fs") : null;
const process = window.require ? window.require('process') : null;

process?.on('SIGINT', () => {
  console.log("process on SIGINT");
  document.getElementById("leave")?.click();
})

if (typeof nw !== 'undefined') {
  nw.Screen.Init();
}

const getUrlParam = (param:string) => {
  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const paramValue = urlParams.get(param);
  return paramValue ? paramValue : "";
};
const joinFromUrl = getUrlParam('join')==='1';
if(joinFromUrl) {
  setTimeout(()=>{
    document.getElementById("join")?.click();
  }, 2000);
}

const channels = [1, 2];
const client = channels.map(()=>AgoraRTC.createClient({ codec: 'h264', mode: 'rtc' }));
console.log("outside function Call");

function Call() {
  console.log("inside function Call");
  const [ appid, setAppid ] = useState(getUrlParam('appid'));
  const [ token, setToken ] = useState(getUrlParam('token'));
  const [ channel, setChannel ] = useState(channels.map(v => getUrlParam('channel'+v)));
  const [ fps, setFps ] = useState(Number(getUrlParam('fps')) || -15);
  const [ recordMode, setRecordMode ] = useState('NO');
  const updateChannel = (index:number, c:string) => {
    let newArr = [...channel];
    newArr[index] = c;
    setChannel(newArr); 
  }
  const agora = client.map(useAgora);
  const agorasVolumes = useAgoraVolumes(client);
  const allChannelRemoteUsers = useMemo(() => {
  //const allChannelRemoteUsers = () => {
    let allusers:IAgoraRTCRemoteUser[] = [];
    agora.forEach(a=>allusers=allusers.concat(a.remoteUsers));
    return allusers;
  }, [agora]);
  
  const mediaRecorder = useRef<any>(null);
  const wStream = useRef<any>(null);
  const recordedChunks = useRef<Array<Blob>>([]);

  const startRecord = (stream:MediaStream) => {
    console.log(stream.getVideoTracks()[0].getSettings());
    //stream.getVideoTracks()[0].applyConstraints({frameRate:Math.abs(fps)});
    let myRecorder = null;
    if (nw.MediaRecorder) {
      myRecorder = new nw.MediaRecorder(stream, 'video/mp4');
    } else {
      myRecorder = new MediaRecorder(stream, {mimeType: 'video/webm; codecs=vp8'});
    }
    mediaRecorder.current = myRecorder;
    recordedChunks.current =[];
    myRecorder.ondataavailable = (event:any) => {
      if (event.data.size > 0) {
        console.log(event);
        if (wStream.current) {
          event.data.arrayBuffer().then((buffer:ArrayBuffer) => wStream.current.write(Buffer.from(buffer)));
        } else {
          recordedChunks.current.push(event.data);
        }
      }
    }
    myRecorder.start(1000, {videoParams:"preset=ultrafast"});
  }

  useEffect(() => {
    console.log("useEffect FPS, allChannelRemoteUsers");
    allChannelRemoteUsers.forEach(user => {
      user.videoTrack?.getMediaStreamTrack().applyConstraints({frameRate:Math.abs(fps)});
    });
  },[fps, allChannelRemoteUsers]);

  //const imageCapturers = useRef<Array<any>>([]);
  
  const videoRoot = useRef(document.createElement('div'));
  useEffect(() => {
    //imageCapturers.current = [];
    console.log("useEffect play videotrack [allChannelRemoteUsers]");
    allChannelRemoteUsers.forEach(user => {
      user.audioTrack?.play();
      if(user.videoTrack) {
        let isTalking = agorasVolumes.maxSpeaker<=0;
        for(let i=0; i<agorasVolumes.remoteVolumes.length && !isTalking;i++) {
          isTalking = user.uid === agorasVolumes.remoteVolumes[i].uid;
        }
        if (isTalking) {
          const video = videoRoot.current.querySelector("#video_" + user.videoTrack.getTrackId()) as HTMLVideoElement;
          if(!video) {
            user.videoTrack.play(videoRoot.current);
          }
        } else {
          user.videoTrack.stop();
        }
        //imageCapturers.current.push(new ImageCapture(user.videoTrack.getMediaStreamTrack()));
      }
    })
    //console.log(imageCapturers.current);
  });

  const getGridCount = () => {
    //return Math.ceil(Math.sqrt(allChannelRemoteUsers.length));
    return Math.ceil(Math.sqrt(videoRoot.current.children.length));
  }

  useEffect(() => {
    console.log("useEffect draw canvas / video element [fps]");
    if (agora[0].joinState) {
      clearTimeout(canvasLooper.current);
      const recodingDiv = document.getElementById('recodingDiv')!;
      if(fps>0) {
        drawCanvas(fps)
      } else {
        const grid = getGridCount();
        const width = recodingDiv.offsetWidth / grid + 'px';
        const height = recodingDiv.offsetHeight / grid + 'px';
        for (let i=0 ; i<videoRoot.current.children.length; i++) {
          const div = videoRoot.current.children[i] as HTMLDivElement;
          div.style.width = width;
          div.style.height = height;
          div.style.float = 'left';
        }
        if(!recodingDiv.contains(videoRoot.current)) {
          recodingDiv.appendChild(videoRoot.current);
        }       
      }
    }
  });

  const canvasLooper = useRef(0);
  const drawCanvas = (fps:number) => {
    const canvas = document.getElementById("recordingCanvas") as HTMLCanvasElement;
    if(!canvas) {
      canvasLooper.current = setTimeout(drawCanvas, 1000/fps, fps);
      return;
    }
    const ctx = canvas.getContext("2d")!;
    ctx.fillStyle = "hsl(0, 0%, 95%)";
    ctx.fillRect(0, 0, canvas.width, canvas.height);
    const videos = allChannelRemoteUsers;
    const gridCount = Math.ceil(Math.sqrt(videos.length));
    const videoWidth = canvas.width / gridCount;
    const videoHeight = canvas.height / gridCount;
    for(let x=0; x < gridCount; x++) {
      for(let y=0; y < gridCount; y++) {
        const vidIdx = y*gridCount + x;
        if (vidIdx >= videos.length) {
          break;
        }
        const videoTrack = videos[vidIdx].videoTrack;
        if (videoTrack) {
          const video = videoRoot.current.querySelector("#video_" + videoTrack.getTrackId()) as HTMLVideoElement;
          if (video) {
            ctx.drawImage(video, x*videoWidth, y*videoHeight, videoWidth, videoHeight);
          }
        }
        /*imageCapturers.current[vidIdx]?.grabFrame().then((imageBitmap:ImageBitmap) => {
          if(imageBitmap) {
            ctx.drawImage(imageBitmap, x*videoWidth, y*videoHeight, videoWidth, videoHeight);
          }
        });*/
      }
    }
    canvasLooper.current = setTimeout(drawCanvas, 1000/fps, fps);
  }

  /*const videoElements = 
      allChannelRemoteUsers.map(user => (<div className='remote-player-wrapper' key={user.uid} style={{float:"left", display:(fps>0?'none':'block')}}>
        <div className='remote-player-text'>{`remoteVideo(${user.uid})`}</div>
        <MediaPlayer videoTrack={user.videoTrack} audioTrack={user.audioTrack} width={1280/getGridCount()} height={720/getGridCount()}></MediaPlayer>
      </div>));*/

  const join = () => {
    console.log("==== join is executed =====");
    client.forEach(c=>c.enableAudioVolumeIndicator());
    agora.forEach((a,i)=>a.join(appid, channel[i], token));
    if(recordMode === 'NO'){
      return;
    }
    if(window.location.protocol === 'chrome-extension:') {
      wStream.current = fs.createWriteStream(nw.MediaRecorder ? 'test.mp4':'test.webm');
    }
    if(window.location.protocol === 'chrome-extension:' && recordMode === 'TAB') {
      chrome.tabCapture.capture({video:true, 
          audio:true, 
          videoConstraints:{
            mandatory:{
              minFrameRate: Math.abs(fps), 
              maxFrameRate: Math.abs(fps),
              minWidth: window.innerWidth,
              maxWidth: window.innerWidth,
              minHeight: window.innerHeight,
              maxHeight: window.innerHeight
            }
          }
        }, (mediaStream) => {
        if(mediaStream === null) {
          console.error("chrome.tabCapture fail");
          return;
        }
        startRecord(mediaStream);
      });              
    } else if (typeof nw !== 'undefined' && recordMode ==='DCM') {
      let dcm = nw.Screen.DesktopCaptureMonitor;
      dcm.once("added", async (id:string, name:string, order:string, type:string) => {
        console.log(id + ", " + name + ", " + order + ", " + type);
        if (name === "Agora Recorder") {
          dcm.stop();
          let constraints = {
            video: {
               mandatory: {
                   chromeMediaSource: 'desktop',
                   chromeMediaSourceId: dcm.registerStream(id),
                   minFrameRate:Math.abs(fps),
                   maxFrameRate:Math.abs(fps)
               }
            }
          };
          try {
            window.navigator.mediaDevices.getUserMedia(constraints as MediaStreamConstraints).then(startRecord);
          } catch (e) {
            console.error(e);
          }
        }
      });
      dcm.start(false, true);
    } else {
      const mediaDevices = navigator.mediaDevices as any;
      mediaDevices.getDisplayMedia({video:{frameRate:Math.abs(fps)}, audio:true}).then(startRecord);
    }
    //chrome.desktopCapture.chooseDesktopMedia(["tab","audio"], (stream:string) => {
    //  console.log(stream);
    //});
  }

  return (
    <div className='call'>
      <form className='call-form' style={joinFromUrl ? {display:'none'} : {}}>
        <label>
          AppID:
          <input type='text' name='appid' value={appid} onChange={(event) => { setAppid(event.target.value) }}/>
        </label>
        <label>
          Token(Optional):
          <input type='text' name='token' value={token} onChange={(event) => { setToken(event.target.value) }} />
        </label>
        {channels.map((c,i) =>(<label key={i}>Channel{c} <input type='text' name={'channel'+c} value={channel[i]} onChange={e => updateChannel(i,e.target.value)} /></label>))}
        <label>
          FPS:
          <input type='number' name='fps' value={fps} onChange={(event) => { 
            const curFps = Number(event.target.value);
            console.log("setFps:" + curFps);
            setFps(curFps);
          }} />
        </label>
        <label>
        Recording Mode:
          <select value={recordMode} onChange={(event) => { setRecordMode(event.target.value) }}>
            <option value="NO">No</option>
            <option value="TAB">Tab</option>
            <option value="DCM">nw.dcm</option>
            <option value="DISPLAYMEDIA">getDisplayMedia</option>
          </select>
        </label>
        <label>
          MaxVideo:
          <input type='number' name='maxVideo' value={agorasVolumes.maxSpeaker} onChange={(event) => { 
            const curMaxVideo = Number(event.target.value);
            console.log("setMaxVideo:" + curMaxVideo);
            agorasVolumes.setMaxSpeaker(curMaxVideo);
          }} />
        </label>        
        <div className='button-group'>
          <button id='join' type='button' className='btn btn-primary btn-sm' disabled={agora[0].joinState} onClick={join}>Join</button>
          <button id='leave' type='button' className='btn btn-primary btn-sm' disabled={!agora[0].joinState} onClick={() => {
            agora.forEach((a)=>a.leave());
            videoRoot.current = document.createElement('div');
            clearTimeout(canvasLooper.current);
            if (mediaRecorder.current) {
              mediaRecorder.current.stop();
              mediaRecorder.current.stream.getTracks().forEach((t:MediaStreamTrack)=>t.stop());
              if (wStream.current) {
                wStream.current.end();
                wStream.current = null;
              } else {
                let blob = new Blob(recordedChunks.current, {
                    type: 'video/webm'
                });
                if (fs) {
                  blob.arrayBuffer().then((buffer:ArrayBuffer) =>fs.writeFileSync('./test.webm', Buffer.from(buffer)));
                } else {
                  var url = URL.createObjectURL(blob);
                  var a = document.createElement('a');
                  document.body.appendChild(a);
                  a.href = url;
                  a.download = 'test.webm';
                  a.click();
                  window.URL.revokeObjectURL(url);
                }
              }
            }
          }}>Leave</button>
        </div>
      </form>
      <div className='player-container'>
      {fps>0 ? <canvas id='recordingCanvas' width={1280} height={720}> </canvas> : <div id='recodingDiv' style={{width:1280, height:720}}/>}
      </div>
    </div>
  );
}

export default Call;
