import { useState, useEffect, useRef } from 'react';
import { IAgoraRTCClient, UID } from 'agora-rtc-sdk-ng';

const getUrlParam = (param:string) => {
  const queryString = window.location.search;
  const urlParams = new URLSearchParams(queryString);
  const paramValue = urlParams.get(param);
  return paramValue ? paramValue : "";
};

export default function useAgoraVolumes(clients: IAgoraRTCClient[]) {

  const [remoteVolumes, setRemoteVolumes] = useState<{level:number, uid:UID}[]>([]);
  const [maxSpeaker, setMaxSpeaker] = useState(Number(getUrlParam('maxvideo')) || 0);
  //const remoteVolumes = useRef<{level:number, uid:UID}[]>([]);
  const arrayRemoteVolumes = useRef<{level:number, uid:UID}[][]>([]);

  useEffect(() => {
    const handleVolumeIndicator = (i:number, volumes:{level:number, uid:UID}[]) => {
      // any better way to init this ?
      if(arrayRemoteVolumes.current.length < clients.length) {
        for(let i=0; i<clients.length; i++) {
          arrayRemoteVolumes.current.push([]);
        }
      }
      arrayRemoteVolumes.current[i] = volumes;
      //remoteVolumes.current = arrayRemoteVolumes.current.flat();
      let newRemoteVolumes = arrayRemoteVolumes.current.flat();
      newRemoteVolumes.sort((a,b) => {
        if(a.level === b.level)
          return String(b.uid).localeCompare(String(a.uid));
        return b.level - a.level;
      });
      //TODO check if the uid has video before slicing it
      newRemoteVolumes = newRemoteVolumes.slice(0, maxSpeaker);
      let arrayIsSame = remoteVolumes.length === newRemoteVolumes.length;
      
      for(let i=0; i<remoteVolumes.length && arrayIsSame; i++) {
        arrayIsSame = remoteVolumes[i].uid === newRemoteVolumes[i].uid;
      }

      if(!arrayIsSame) {
        setRemoteVolumes(newRemoteVolumes);
      }
    }
    if (maxSpeaker>0) {
      clients.forEach((c,i)=>c.on('volume-indicator', (volumes:{level:number, uid:UID}[]) => {
        handleVolumeIndicator(i,volumes);
      }));
    }

    return () => {
      // destructor
      if (maxSpeaker>0) {
        clients.forEach((c,i)=>c.off('volume-indicator', (volumes:{level:number, uid:UID}[]) => {
          handleVolumeIndicator(i,volumes);
        }));
      }
      setRemoteVolumes([]);
    };
  }, [clients, maxSpeaker]);

  return {remoteVolumes, maxSpeaker, setMaxSpeaker};
}